    niceString(valor) {
      var valorKomma;
      if (valor > 1000) {
        var tusiger = Math.floor(valor / 1000);
        var rest = valor % 1000;
        var restStr;
        if (rest < 10) {
          restStr = "00" + rest.toString();
        } else if (rest < 100) {
          restStr = "0" + rest.toString();
        } else {
          restStr = rest.toString();
        }
        valorKomma = tusiger.toString() + "." + restStr;
      } else {
        valorKomma = valor.toString();
      }
      return "R$ " + valorKomma + ",00";
    },
    formaStr(forma) {
      if (forma === 1) {
        return "Dinheiro";
      } else if (forma === 2) {
        return "Carto de Dbito";
      } else if (forma === 3) {
        return "Carto de Crdito";
      } else if (forma === 4) {
        return "Transferncia Bancria";
      } else if (forma === 5) {
        return "PIX";
      } else {
        return "forma de pagamento no especificada";
