    stringify(min) {
      if (min < 10) {
        return "0" + min.toString();
      } else {
        return min;
      }
    },
    readableStart(startStr) {
      var diasPtBr = ["Dom.", "Seg.", "Ter.", "Qua.", "Qui.", "Sex.", "Sab."];
      var date = new Date(startStr);
      var dayName = diasPtBr[date.getDay()];
      var day = date.getDate();
      var dayStr;
      if (day < 10) {
        dayStr = "0" + day.toString();
      } else {
        dayStr = day.toString();
      }
      var month = date.getMonth() + 1;
      var monthStr;
      if (month < 10) {
        monthStr = "0" + month.toString();
      } else {
        monthStr = month.toString();
      }
      var year = date.getYear() - 100;
      var yearStr = year.toString();
      var hoursStr = date.getHours().toString();
      var min = date.getMinutes();
      var minStr;
      if (min < 10) {
        minStr = "0" + min.toString();
      } else {
        minStr = min.toString();
      }

      var str =
        dayName +
        ", " +
        dayStr +
        "/" +
        monthStr +
        "/" +
        yearStr +
        ", " +
        hoursStr +
        ":" +
        minStr;

      return str;
    },
    readableEnd(endStr) {
      var date = new Date(endStr);
      var hoursStr = date.getHours().toString();
      var min = date.getMinutes();
      var minStr;
      if (min < 10) {
        minStr = "0" + min.toString();
      } else {
        minStr = min.toString();
      }

      var str = hoursStr + ":" + minStr;

      return str;
    },
