      imgWidth = 75;
      imgHeight =
        imgWidth /
        (this.preloadSignature.naturalWidth /
          this.preloadSignature.naturalHeight);
      imgPos = (210 - imgWidth) / 2;
      textStartYPos =
        imgHeight +
        // 4 * (imgHeight / fontSize) +
        endTablePosY +
        16 +
        imgHeight * 1.4;

      doc.addImage({
        imageData: this.preloadSignature,
        y: endTablePosY + 16 + imgHeight * 1.4,
        x: imgPos,
        w: imgWidth,
        h: imgHeight,
      });

      createTexts(
        [
          "____________________________",
          `Nutricionista ${this.$store.state.firestore.userInfo.name}`,
          `CRN: ${this.$store.state.firestore.userInfo.crn}`,
          `Data: ${day}/${month}/${year}`,
        ],
        true,
        textStartYPos
      );

      doc.save("exame.pdf");
    },
