<template>
  <div>
    <v-dialog v-model="visualizarDialog" max-width="80%" height="80%" multiple>
      <v-card>
        <v-card-title>
          {{ examPVis.nome }}, ({{ examPVis.dateStr }})
        </v-card-title>
        <v-card-text>
          <v-textarea readonly v-model="examPVis.examText"> </v-textarea>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-card elevation="5">
      <v-card-title class="text-h6"> Exames prescritas </v-card-title>
      <v-card-text>
        <v-card v-for="examP in exames" :key="examP.nome" outlined>
          <v-row class="d-flex justify-center">
            <v-col cols="12" class="d-flex justify-space-between">
              <v-row class="text-subtitle-1 ml-2 d-flex align-center">
                {{ examP.titulo }} ({{ examP.dateStr }})
              </v-row>

              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="#614021"
                    v-bind="attrs"
                    v-on="on"
                    @click="downloadPdf(examP)"
                  >
                    <v-icon color="#c5d13f">mdi-download</v-icon>
                  </v-btn>
                </template>
                <span>Baixar Prescrio</span>
              </v-tooltip>

              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="#c5d13f"
                    v-bind="attrs"
                    v-on="on"
                    @click="setPrescricaoVisualizar(examP.id)"
                    class="mr-2 mx-2 black--text"
                  >
                    <v-icon color="#614021">mdi-eye</v-icon>
                  </v-btn>
                </template>
                <span>Visualizar Prescrio</span>
              </v-tooltip>
            </v-col>
          </v-row>
        </v-card>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import firebase from "firebase";
import jspdf from "jspdf";
import "jspdf-autotable";

export default {
  data() {
    return {
      formulas: [],
      visualizarDialog: false,
      panel: [],
      preloadLogo: new Image(),
      preloadSignature: new Image(),
    };
  },
  firestore() {
    return {
      formulas: firebase
        .firestore()
        .collection("users")
        .doc(sessionStorage.getItem("current-user"))
        .collection("formulas"),
    };
  },
  computed: {
    examPVis() {
      return this.$store.state.firestore.examPVis;
    },
  },
  methods: {
    async preloadImages() {
      //   console.log(this.$store.state.firestore.pacienteNutriLogo);
      //   this.preloadLogo.crossOrigin = "anonymous";
      //   this.preloadSignature.crossOrigin = "anonymous";
      //   this.preloadLogo.src = `${this.$store.state.firestore.pacienteNutriLogo}`;
      //   this.preloadSignature.src = `${this.$store.state.firestore.pacienteNutriSign}`;

      this.preloadSignature.src = require("@/assets/signature_thayana.jpg");
      this.preloadLogo.src = require("@/assets/logoCompleto.png");

      this.preloadLogo.onload = function () {
        console.log("Logo carregada");
      };
      this.preloadSignature.onload = function () {
        console.log("Signature carregada");
      };

      console.log(
        "natural:",
        this.preloadLogo.naturalWidth,
        this.preloadLogo.naturalHeight
      );
      console.log(
        "natural:",
        this.preloadSignature.naturalWidth,
        this.preloadSignature.naturalHeight
      );
    },
    async downloadPdf(examP) {
      alert("Gerando PDF");
      // links
      // https://mrrio.github.io/jsPDF/examples/basic.html
      // http://www.rotisedapsales.com/snr/cloud_staging/website/jsPDF-master/docs/global.html#text
      // https://simonbengtsson.github.io/jsPDF-AutoTable/
      // https://www.npmjs.com/package/jspdf-autotable

      var doc = new jspdf("p");
      var date = new Date();
      var day = date.getDate();
      var month = date.getMonth() + 1;
      var year = date.getFullYear();

      // Doc settings
      var fontSize = 12;
      doc.setFontSize(fontSize);
      doc.setFont("Times");

      // The (900 / 262) division is used to keep the proportion of the original image
      // New Width * (900 / 262) will keep the height proportion

      var imgWidth = 40;
      var imgHeight = imgWidth / (900 / 262);
      var imgPos = (210 - imgWidth) / 2;
      var startXPos = 15;
      var textStartYPos = imgHeight * 4;

      var centeredText = function (text, y) {
        var textWidth =
          (doc.getStringUnitWidth(text) * doc.internal.getFontSize()) /
          doc.internal.scaleFactor;
        var textOffset = (doc.internal.pageSize.width - textWidth) / 2;
        doc.text(textOffset, y, text);
      };

      var createTexts = function (
        textList,
        centered = false,
        textStart = textStartYPos
      ) {
        for (var i = 0; i < textList.length; i++) {
          if (centered) {
            centeredText(textList[i], textStart + (fontSize / 2) * (1 + i));
          } else {
            var lines = doc.setFont("Roboto").splitTextToSize(textList[i], 180);
            // console.log(lines)
            for (var j = 0; j < lines.length; j++) {
              doc.text(
                startXPos,
                textStart + (fontSize / 2) * (1 + j),
                lines[j]
              );
            }
            textStart += (fontSize / 2) * /*1 + */ lines.length;
          }
        }
        return (textStart += fontSize / 2);
      };

      // console.log(this.$store.state.firestore.userInfo);
      // // Logo Clubedonutricionista
      // if (this.$store.state.firestore.userInfo.useLogo === false) {
      // console.log("op 1");
      doc.addImage({
        imageData: require("@/assets/logoCompleto.png"),
        y: startXPos,
        x: imgPos,
        w: imgWidth,
        h: imgHeight,
      });
      // } else {
      //   // LOGO CLUBE
      //   imgWidth = 30;
      //   imgHeight = imgWidth / (900 / 262);
      //   startXPos = 15;

      //   doc.addImage({
      //     imageData: require("@/assets/logoCompleto.png"),
      //     y: startXPos,
      //     x: 15,
      //     w: imgWidth,
      //     h: imgHeight,
      //   });

      //   imgWidth = 50;
      //   var startYPos = 15 + imgHeight * 2;
      //   imgHeight =
      //     imgWidth /
      //     (this.preloadSignature.naturalWidth /
      //       this.preloadSignature.naturalHeight);
      //   imgPos = (210 - imgWidth) / 2;
      //   textStartYPos = imgHeight + 4 * (imgHeight / fontSize) + startYPos;

      //   doc.addImage({
      //     imageData: this.preloadSignature,
      //     y: startYPos,
      //     x: imgPos,
      //     w: imgWidth,
      //     h: imgHeight,
      //   });
      // }

      var texts = [
        `Prescrio de ${this.nomeExamP} (${day}/${month}/${year})`,
        `Por: ${this.pacienteChosen.data.nome}`,
      ];

      var endTablePosY = createTexts(texts);

      endTablePosY = createTexts([examP.examText], false, endTablePosY);

      endTablePosY = createTexts([examP.observacoes], false, endTablePosY);

      // USE SIGNATURE
      // console.log("signature", this.$store.state.firestore.userInfo.signature);
      // if (this.$store.state.firestore.userInfo.signature != undefined) {
      //   imgWidth = 30;
      //   startYPos =
      //     fontSize +
      //     (endTablePosY !== undefined
      //       ? endTablePosY + 16
      //       : textStartYPos + texts.length * (fontSize * (3 / 4)));
      //   imgHeight =
      //     imgWidth /
      //     (this.preloadLogo.naturalWidth / this.preloadLogo.naturalHeight);
      //   imgPos = (210 - imgWidth) / 2;
      //   textStartYPos = imgHeight + 4 * (imgHeight / fontSize) + startYPos;

      //   doc.addImage({
      //     imageData: this.preloadSignature,
      //     y: startYPos,
      //     x: imgPos,
      //     w: imgWidth,
      //     h: imgHeight,
      //   });
      // } else {
      // imgHeight = 0;
      // }

      imgWidth = 75;
      imgHeight =
        imgWidth /
        (this.preloadSignature.naturalWidth /
          this.preloadSignature.naturalHeight);
      imgPos = (210 - imgWidth) / 2;
      textStartYPos =
        imgHeight +
        // 4 * (imgHeight / fontSize) +
        endTablePosY +
        16 +
        imgHeight * 1.4;

      doc.addImage({
        imageData: this.preloadSignature,
        y:
          endTablePosY +
          16 +
          imgHeight * 1.4,
        x: imgPos,
        w: imgWidth,
        h: imgHeight,
      });

      createTexts(
        [
          "____________________________",
          `Nutricionista ${this.$store.state.firestore.userInfo.name}`,
          `CRN: ${this.$store.state.firestore.userInfo.crn}`,
          `Data: ${day}/${month}/${year}`,
        ],
        true,
        textStartYPos
      );

      doc.save("exame.pdf");
    },
    setPrescricaoVisualizar(docId) {
      this.$store.dispatch("firestore/getExamePaciente", {
        docId: docId,
      });

      this.visualizarDialog = true;
    },
  },
};
</script>