                      label="Frutas"
                      color="#c5d13f"
                    ></v-checkbox>
                    <v-checkbox
                      hide-details
                      v-model="pacienteInfos.lanches"
                      value="Castanhas"
                      label="Castanhas"
                      color="#c5d13f"
                    ></v-checkbox>
                    <v-checkbox
                      hide-details
                      v-model="pacienteInfos.lanches"
                      value="Pasta de amendoim"
                      label="Pasta de amendoim"
                      color="#c5d13f"
                    ></v-checkbox>
                    <v-checkbox
                      hide-details
                      v-model="pacienteInfos.lanches"
                      value="Iogurte natural"
                      label="Iogurte natural"
                      color="#c5d13f"
                    ></v-checkbox>
                    <v-checkbox
                      hide-details
                      v-model="pacienteInfos.lanches"
                      value="Sementes como chia / linhaa"
                      label="Sementes como chia / linhaa"
                      color="#c5d13f"
                    ></v-checkbox>
                    <v-checkbox
