                      v-model="pacienteInfos.lanches"
                      value="Bolos ou bolachas integrais"
                      label="Bolos ou bolachas integrais"
                      color="#c5d13f"
                    ></v-checkbox>
                    <v-checkbox
                      hide-details
                      v-model="pacienteInfos.lanches"
                      value="Torrada integral"
                      label="Torrada integral"
                      color="#c5d13f"
                    ></v-checkbox>
                    <v-checkbox
                      hide-details
                      v-model="pacienteInfos.lanches"
                      value="Ovos mexidos ou cozidos"
                      label="Ovos mexidos ou cozidos"
