                        v-model="pacienteInfos.frutasGostar"
                        label="..."
                      >
                      </v-text-field>
                    </v-container>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-container fluid>
                      <p>
                        Que <strong>frutas</strong> voc
                        <strong>no</strong> tolera?
                      </p>
                      <v-text-field
                        solo
                        v-model="pacienteInfos.frutasNaoTolerar"
                        label="..."
                      >
                      </v-text-field>
                    </v-container>
                  </v-col>
                </v-row>
                <v-divider></v-divider>
