                        solo
                        v-model="pacienteInfos.almocoFeijao"
                        label="..."
                      >
                      </v-text-field>
                    </v-container>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-container fluid>
                      <p>
                        Gosta de <strong>saladas</strong> cruas, refogadas e
