                      </p>
                      <v-text-field
                        solo
                        v-model="pacienteInfos.almocoSalada"
                        label="..."
                      >
                      </v-text-field>
                    </v-container>
                  </v-col>
                </v-row>
                <v-row class="d-flex justify-center">
                  <v-col cols="12" md="6">
                    <v-container fluid>
                      <p>
                        Tem <strong>averso</strong> por algum
                        <strong>vegetal</strong>? Quais?
                      </p>
                      <v-text-field
                        solo
                        v-model="pacienteInfos.aversaoVegetal"
                        label="..."
                      >
                      </v-text-field>
                    </v-container>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-container fluid>
                      <p>
                        Gosta de outras leguminosas como gro de bico, lentilha
                        e/ou ervilha?
                      </p>
                      <v-text-field
                        solo
                        v-model="pacienteInfos.leguminosas"
                        label="..."
                      >
                      </v-text-field>
                    </v-container>
                  </v-col>
                </v-row>
                <v-row class="d-flex justify-center">
                  <v-col cols="12" md="6">
                    <v-container fluid>
                      <p>
                        Costuma comer
                        <strong>purs, quinoa e/ou razes</strong> como batata
                        doce e macaxeira?
                      </p>
                      <v-text-field
                        solo
                        v-model="pacienteInfos.pures"
                        label="..."
                      >
                      </v-text-field>
                    </v-container>
                  </v-col>
                  <v-col cols="12" md="6">
                    <v-container fluid>
                      <p>
                        Gosta de <strong>massas</strong> tipo macarronada,
                        lasanha etc?
                      </p>
                      <v-text-field
                        solo
                        v-model="pacienteInfos.massas"
                        label="..."
                      >
                      </v-text-field>
                    </v-container>
                  </v-col>
                </v-row>
                <v-row>
                  <v-container fluid>
                    <p>Gosta de frango, carne vermelha e peixe?</p>
                    <v-text-field
                      solo
                      v-model="pacienteInfos.frangoCarnePeixe"
                      label="..."
                    >
                    </v-text-field>
                  </v-container>
                </v-row>
                <v-divider></v-divider>
                <v-row class="d-flex justify-center">
                  <v-col cols="12">
                    <v-row class="d-flex justify-center">
                      <p>Sobre o <strong>jantar</strong>:</p>
                    </v-row>
                  </v-col>
                  <v-container fluid>
                    <p>
                      Gosta de comer
                      <strong>o mesmo do almoo ou lanches</strong>? Ou os dois?
                    </p>
                    <v-checkbox
                      hide-details
                      v-model="pacienteInfos.jantar"
                      value="O mesmo do almoo"
                      label="O mesmo do almoo"
                      color="#c5d13f"
                    ></v-checkbox>
                    <v-checkbox
                      hide-details
                      v-model="pacienteInfos.jantar"
                      value="Somente lanche"
                      label="Somente lanche"
                      color="#c5d13f"
                    ></v-checkbox>
                    <v-checkbox
                      hide-details
                      v-model="pacienteInfos.jantar"
                      value="Gosto de sopas"
