                  Primeira Anamnese, {{ nowDateStr }}
                </v-card-title>
                <v-card-text>
                  <v-textarea
                    solo
                    v-model="anamneseContent"
                    height="300"
                  ></v-textarea>
                </v-card-text>
              </v-card>
            </div>
            <div v-else>
              <v-card
                v-for="note in allAnamneseNotes"
                :key="note.index"
                style="background-color: rgba(197, 209, 63, 0.6)"
                class="my-2"
              >
                <v-card-title>
                  Anamnese nmero {{ note.index }}, {{ note.dateStr }}
                </v-card-title>
                <v-card-text>
                  <v-textarea
                    solo
                    v-model="note.content"
                    height="300"
                  ></v-textarea>
                </v-card-text>
              </v-card>
              <v-card
                style="background-color: rgba(197, 209, 63, 0.6)"
                class="my-2"
              >
                <v-card-title>
                  Anamnese nmero {{ anamnesesNr + 1 }},
                  {{ nowDateStr }}
                </v-card-title>
                <v-card-text>
                  <v-textarea
                    solo
                    v-model="anamneseContent"
                    height="300"
                  ></v-textarea>
                </v-card-text>
              </v-card>
            </div>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </v-row>

    <v-row class="d-flex justify-center">
      <v-btn large color="#c5d13f" class="my-6" @click="submit">
        Concluir
      </v-btn>
    </v-row>
  </div>
</template>

<script>
import firebase from "firebase";
import {
  mdiAccount,
  mdiWeb,
  mdiLock,
  mdiEmail,
  mdiEye,
  mdiEyeOff,
  mdiStore,
  mdiAlert,
  mdiCalendar,
  mdiPhone,
  mdiWhatsapp,
  mdiMapMarker,
  mdiCity,
  mdiFlagVariantOutline,
  mdiPiggyBank,
  mdiHandHeart,
  mdiBabyCarriage,
  mdiHumanMaleChild,
  mdiHospitalBuilding,
  mdiAccountVoice,
  mdiWeightKilogram,
  mdiHumanMaleHeightVariant,
  mdiWeightLifter,
} from "@mdi/js";

export default {
  data: function () {
    return {
      valid: true,
      estadoCivilItems: [
        "-",
        "Solteira/Solteiro",
        "Casada/Casado",
        "Viva/Vivo",
        "Divorciada/Divorciado",
      ],
      panel: [],
      anamneseContent: "",
      pacienteFirestore: {},
      icons: {
        mdiWeb,
        mdiLock,
        mdiEmail,
        mdiEye,
        mdiEyeOff,
        mdiStore,
        mdiAccount,
        mdiAlert,
        mdiCalendar,
        mdiPhone,
        mdiWhatsapp,
        mdiMapMarker,
        mdiCity,
        mdiFlagVariantOutline,
        mdiPiggyBank,
        mdiHospitalBuilding,
        mdiHandHeart,
        mdiBabyCarriage,
        mdiHumanMaleChild,
        mdiAccountVoice,
        mdiWeightKilogram,
        mdiHumanMaleHeightVariant,
        mdiWeightLifter,
      },
    };
  },
  computed: {
    pacientePreAtendimento: function () {
      return this.$store.state.firestore.pacientePreAtendimento;
    },
    pacienteInfos: function () {
      return this.$store.state.firestore.pacienteInfos;
    },
    nowDateStr() {
      var d = new Date();
      var day = d.getDate();
      var month = d.getMonth() + 1;
      var year = d.getFullYear();

      var dayStr;
      if (day < 10) {
        dayStr = "0" + day.toString();
      } else {
        dayStr = day.toString();
      }
      var monthStr;
      if (month < 10) {
        monthStr = "0" + month.toString();
      } else {
        monthStr = month.toString();
      }

      var dateStr = dayStr + "/" + monthStr + "/" + year.toString();

      return dateStr;
    },
    anamnesesNr: function () {
      return this.$store.state.firestore.anamnesesNr;
    },
    allAnamneseNotes: function () {
      return this.$store.state.firestore.allAnamneseNotes;
    },
  },
  methods: {
    submit() {
      if (this.$refs.form.validate()) {
        this.$store.dispatch(
          "firestore/pacienteAtendimento",
          this.pacientePreAtendimento
        );

        this.$store.dispatch("firestore/nutriPreAtendimentoSubmit", {
          pacienteInfos: this.pacienteInfos,
          pacienteEmail: this.pacientePreAtendimento,
        });

        if (this.anamneseContent !== "") {
          var d = new Date();
          this.$store.dispatch("firestore/nutriAnamneseSubmit", {
            pacienteEmail: this.pacientePreAtendimento,
            content: this.anamneseContent,
            index: this.anamnesesNr + 1,
            dateStr: this.nowDateStr,
            millis: d.getTime(),
          });
        }

        // this.$router.push('home') in actions nutriPreAtendimentoSubmit-function: to="NovoAtendimento"
      } else {
        alert("Preenche todos os campos de forma correta");
      }
    },
  },
  firestore: function () {
    return {
      pacienteFirestore: firebase
        .firestore()
        .collection("users")
        .doc(sessionStorage.getItem("current-user"))
        .collection("pacientes")
        .doc(this.pacientePreAtendimento),
    };
  },
  mounted() {
    console.log(this.pacientePreAtendimento);
    this.$store.dispatch("firestore/nutriGetPacienteInfos", {
      pacienteEmail: this.pacientePreAtendimento,
    });
    this.$store.dispatch("firestore/nutriGetAnamnesesNr", {
      pacienteEmail: this.pacientePreAtendimento,
    });
    this.$store.dispatch("firestore/getAllAnamneseNotes", {
      pacienteEmail: this.pacientePreAtendimento,
    });
  },
};
</script>

